@import "base/reset.css";
:root {
  --primary-color:$primary;
}

body.blue {
  --primary-color:#004387;
}

body.green {
  --primary-color:#00876e;
}

.full-view {
  height: 100vh;
  width: 100vw;
}

.flex {
  display: flex;
}

.vertical {
  flex-direction: column;
}

.center {
  justify-content: center;
}

.fade-out {
  opacity: 0;
}

@keyframes revealChar {
  0% {
    transform: translateY(102%) translateZ(0) translate3d(0, 0, 0);
  }
  100% {
    transform: translateY(0) translateZ(0) translate3d(0, 0, 0);
  }
}
@keyframes rotateWrapper {
  0% {
    transform: rotateY(110deg);
    filter: blur(20px);
  }
  100% {
    transform: rotateY(0deg);
    filter: blur(0);
  }
}
@keyframes blurIn {
  0% {
    filter: blur(20px);
  }
  50% {
    filter: blur(14px);
  }
  100% {
    filter: blur(0);
  }
}
@keyframes zFadeIn {
  0% {
    transform: translateZ(-200px) perspective(1000px);
    opacity: 0;
  }
  100% {
    transform: translateZ(0) perspective(1000px);
    opacity: 1;
  }
}
@keyframes clipSwipe {
  0% {
    clip-path: polygon(0 0, 0 0, 0 100%, 0 100%);
  }
  100% {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
}
.clip-swipe {
  clip-path: polygon(0 0, 0 0, 0 100%, 0 100%);
}
.clip-swipe.active {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}

.fade-reveal {
  opacity: 0;
  transition: all 1s, height 0s, width 0s;
  transition-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);
}
.fade-reveal.active {
  opacity: 1;
  transition-delay: 0.4s;
}
.fade-reveal[data-delay="1"] {
  transition-delay: 1s;
}
.fade-reveal[data-delay="2"] {
  transition-delay: 2s;
}
.fade-reveal[data-delay="3"] {
  transition-delay: 3s;
}
.fade-reveal[data-delay="4"] {
  transition-delay: 4s;
}
.fade-reveal[data-delay="5"] {
  transition-delay: 5s;
}
.fade-reveal[data-delay="6"] {
  transition-delay: 6s;
}
.fade-reveal[data-delay="7"] {
  transition-delay: 7s;
}
.fade-reveal[data-delay="8"] {
  transition-delay: 8s;
}
.fade-reveal[data-delay="9"] {
  transition-delay: 9s;
}
.fade-reveal[data-delay="10"] {
  transition-delay: 10s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.fade-in {
  animation: fadeIn 1s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

@keyframes rotateCounterClockwise {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-359.999deg);
  }
}
@font-face {
  font-family: "Evolve";
  font-weight: normal;
  src: url("../assets/fonts/MADE Evolve Sans Regular.woff") format("woff");
}
@font-face {
  font-family: "Visby";
  font-weight: normal;
  src: url("../assets/fonts/VisbyCF-Bold.woff") format("woff");
}
@font-face {
  font-family: "Kenfolg";
  src: url("../assets/fonts/MADE Kenfolg v2.woff") format("woff");
}
body *,
body *:before,
body *:after {
  font-family: "Visby", "Helvetica", "Arial", sans-serif;
}
body p {
  font-family: "Evolve", "Open Sans", "Helvetica", "Arial", sans-serif;
}
body.loading main #index.page h1,
body.loading main #index.page h2,
body.loading main #index.page h3,
body.loading main #index.page h4,
body.loading main #index.page h5,
body.loading main #index.page p,
body.loading main #index.page span,
body.loading main #index.page li,
body.loading main #index.page a {
  transition: 0.24s ease;
}
body h1 {
  font-size: 140px;
  margin-bottom: 40px;
}
@media (max-width: 1368px) {
  body h1 {
    font-size: 70px;
  }
}
@media (max-width: 768px) {
  body h1 {
    font-size: 10vw;
  }
}
@media (max-width: 480px) {
  body h1 {
    font-size: 10vw;
    max-width: calc(100% - 32px);
    margin-left: auto;
    margin-right: auto;
  }
}
body h1 .char {
  margin: 0 -4px;
}
@media (max-width: 1368px) {
  body h1 .char {
    margin: 0 -1.5px;
  }
}
@media (max-width: 768px) {
  body h1 .char {
    margin: 0 -0.2vw;
  }
}
body h2 {
  font-size: 80px;
  margin-bottom: 32px;
}
@media (max-width: 1368px) {
  body h2 {
    font-size: 40px;
  }
}
@media (max-width: 768px) {
  body h2 {
    font-size: 6vw;
  }
}
@media (max-width: 480px) {
  body h2 {
    max-width: calc(100% - 32px);
    margin-left: auto;
    margin-right: auto;
  }
}
body h2 .char {
  margin: 0 -2px;
}
@media (max-width: 1368px) {
  body h2 .char {
    margin: 0 -1px;
  }
}
@media (max-width: 768px) {
  body h2 .char {
    margin: 0 0;
  }
}
body h1,
body h2 {
  cursor: default;
}
body h5 {
  font-size: 18px;
}
@media (max-width: 768px) {
  body h5 {
    font-size: 16px;
  }
}
@media (max-width: 480px) {
  body h5 {
    font-size: 14px;
  }
}
body p {
  font-size: 40px;
  margin: 0 auto;
  text-align: center;
  max-width: calc(100vw - 32px);
  transition: 0.24s ease;
}
@media (max-width: 1368px) {
  body p {
    font-size: 20px;
  }
}
@media (max-width: 768px) {
  body p {
    font-size: 18px;
  }
}
@media (max-width: 480px) {
  body p {
    font-size: 16px;
  }
}
body p span {
  font-size: inherit;
  font-weight: inherit;
  font-family: inherit;
}
body span.char {
  backface-visibility: hidden;
  transform: translateZ(0) translate3d(0, 0, 0);
  -webkit-backface-visibility: hidden;
  -webkit-perspective: 1000;
}
body span.char:before {
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  -webkit-perspective: 1000;
  will-change: transform;
  transform: translateY(102%) translateZ(0) translate3d(0, 0, 0);
}

h2.titles-wrapper {
  position: relative;
}
h2 .title {
  opacity: 0;
  position: absolute;
  transition: 0.24s ease;
  transition: position 0s, all 0.4s;
  left: 0;
  right: 0;
}
h2 .title.active {
  opacity: 1;
  position: relative;
  transition: position 0s, all 0.4s;
  transition-delay: 0.3s;
}

.project.page p,
#about.page p,
#contact.page p {
  font-size: 32px;
  width: 1200px;
  max-width: calc(100% - 32px);
  margin: 32px auto;
  text-align: center;
}
@media (max-width: 1368px) {
  .project.page p,
  #about.page p,
  #contact.page p {
    font-size: 20px;
    width: 734px;
  }
}
@media (max-width: 991px) {
  .project.page p,
  #about.page p,
  #contact.page p {
    width: 672px;
  }
}
@media (max-width: 480px) {
  .project.page p,
  #about.page p,
  #contact.page p {
    font-size: 16px;
    width: calc(100vw - 32px);
  }
}

.splitting ::selection {
  background: transparent;
}

span.char {
  overflow: hidden;
  opacity: 1;
  visibility: hidden;
  padding: 4px 0;
}
@media (max-width: 1368px) {
  span.char {
    padding: 2px 0;
  }
}
@media (max-width: 480px) {
  span.char {
    padding: 0px 0 1px;
  }
}
span.char:before {
  transform: translateY(102%);
  visibility: visible !important;
  animation-delay: calc(0.04s * var(--char-index)) !important;
}
span.char.reveal-char {
  animation: revealChar 1s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.splitting.reveal span.char:before {
  animation: revealChar 0.42s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.splitting-rows {
  -webkit-transform: perspective(1000px);
  perspective: 1000px;
  opacity: 0;
}
.splitting-rows.reveal {
  opacity: 1;
}
.splitting-rows.reveal span.word {
  animation: zFadeIn 1.4s;
  animation-delay: calc(0.1s * var(--line-index) + 2s) !important;
}
.splitting-rows span.word {
  opacity: 0;
}

.splitting.blurRotate span.char:before {
  transform: translateY(102%);
  visibility: visible !important;
}
.splitting.blurRotate span.char.reveal-char {
