@font-face {
    font-family: "Evolve";
    font-weight: normal;
    src: url("../assets/fonts/MADE Evolve Sans Regular.woff") format("woff")
}

@font-face {
    font-family: "Visby";
    font-weight: normal;
    src: url("../assets/fonts/VisbyCF-Bold.woff") format("woff")
}

@font-face {
    font-family: "Kenfolg";
    src: url("../assets/fonts/MADE Kenfolg v2.woff") format("woff")
}


body {

    *,
    *:before,
    *:after {
        font-family: 'Visby', 'Helvetica', 'Arial', sans-serif;
    }

    p {
        font-family: 'Evolve', 'Open Sans', 'Helvetica', 'Arial', sans-serif;
    }

    &.loading {
        main #index.page {

            h1,
            h2,
            h3,
            h4,
            h5,
            p,
            span,
            li,
            a {
                @include smooth;
            }
        }
    }

    h1 {
        font-size: 140px;
        margin-bottom: 40px;

        @media (max-width:1368px) {
            font-size: 70px;
        }

        @media (max-width:768px) {
            //font-size:36px;
            font-size: 10vw;
        }

        @media (max-width:480px) {
            //font-size:28px;
            font-size: 10vw;
            max-width: calc(100% - 32px);
            margin-left: auto;
            margin-right: auto;
        }

        .char {
            margin: 0 -4px;

            @media (max-width:1368px) {
                margin: 0 -1.5px;
            }

            @media (max-width:768px) {
                margin: 0 -0.2vw;
            }
        }
    }

    h2 {
        font-size: 80px;
        margin-bottom: 32px;

        @media (max-width:1368px) {
            font-size: 40px;
        }

        @media (max-width:768px) {
            font-size: 6vw;
        }

        @media (max-width:480px) {
            max-width: calc(100% - 32px);
            margin-left: auto;
            margin-right: auto;
        }

        .char {
            margin: 0 -2px;

            @media (max-width:1368px) {
                margin: 0 -1px;
            }

            @media (max-width:768px) {
                margin: 0 0;
            }
        }
    }

    h1,
    h2 {
        cursor: default;
    }

    h5 {
        font-size: 18px;

        @media (max-width:768px) {
            font-size: 16px;
        }

        @media (max-width:480px) {
            font-size: 14px;
        }
    }

    p {
        font-size: 40px;
        margin: 0 auto;
        text-align: center;
        max-width: calc(100vw - 32px);
        @include smooth;

        @media (max-width:1368px) {
            font-size: 20px;
        }

        @media (max-width:768px) {
            font-size: 18px;
        }

        @media (max-width:480px) {
            font-size: 16px;
        }

        span {
            font-size: inherit;
            font-weight: inherit;
            font-family: inherit;
        }
    }

    span.char {
        backface-visibility: hidden;
        transform: translateZ(0) translate3d(0, 0, 0);
        -webkit-backface-visibility: hidden;
        -webkit-perspective: 1000;

        &:before {
            backface-visibility: hidden;
            -webkit-backface-visibility: hidden;
            -webkit-perspective: 1000;
            will-change: transform;
            transform: translateY(102%) translateZ(0) translate3d(0, 0, 0);
        }
    }
}

h2 {
    &.titles-wrapper {
        position: relative;
    }

    .title {
        opacity: 0;
        position: absolute;
        @include smooth;
        transition: position 0s, all 0.4s;
        left: 0;
        right: 0;

        &.active {
            opacity: 1;
            position: relative;
            transition: position 0s, all 0.4s;
            transition-delay: 0.3s;
        }
    }
}

.project.page,
#about.page,
#contact.page {
    p {
        font-size: 32px;
        width: 1200px;
        max-width: calc(100% - 32px);
        margin: 32px auto;
        text-align: center;

        @media (max-width:1368px) {
            font-size: 20px;
            width: 734px;
        }

        @media (max-width:991px) {
            width: 672px;
        }

        @media (max-width:480px) {
            font-size: 16px;
            width: calc(100vw - 32px);
        }
    }
}

.splitting {
    ::selection {
        background: transparent;
    }
}

span {
    &.char {
        overflow: hidden;
        opacity: 1;
        visibility: hidden;
        padding: 4px 0;

        @media (max-width:1368px) {
            padding: 2px 0;
        }

        @media (max-width:480px) {
            padding: 0px 0 1px;
        }

        &:before {
            transform: translateY(102%);
            visibility: visible !important;
            animation-delay: calc(0.04s * var(--char-index)) !important;
        }

        &.reveal-char {
            animation: revealChar 1s;
            animation-fill-mode: forwards;
            animation-timing-function: cubic-bezier(.785, 0.135, 0.15, 0.86);
        }
    }

}

.splitting {
    &.reveal {
        span.char {
            &:before {
                animation: revealChar 0.42s;
                animation-fill-mode: forwards;
                animation-timing-function: cubic-bezier(.785, 0.135, 0.15, 0.86);
            }
        }
    }
}

.splitting-rows {
    -webkit-transform: perspective(1000px);
    perspective: 1000px;
    opacity: 0;

    &.reveal {
        opacity: 1;

        span.word {
            animation: zFadeIn 1.4s;
            animation-delay: calc(0.1s * var(--line-index) + 2s) !important;
        }
    }

    span.word {
        opacity: 0;
    }
}

.splitting.blurRotate {
    span {
        &.char {
            &:before {
                transform: translateY(102%);
                visibility: visible !important;
            }

            &.reveal-char {
                animation: revealChar 1s;
                animation-fill-mode: forwards;
                animation-timing-function: cubic-bezier(.785, 0.135, 0.15, 0.86);
            }
        }

        &.word {
            @for $i from 1 through 40 {
                &:nth-of-type(#{$i}) {
                    span.char {
                        @for $x from 1 through 40 {
                            &:nth-of-type(#{$x}) {
                                &:before {
                                    animation-delay:#{ ($x * 0.028s) + ($i * 0.04s) } !important; // fast
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    &.reveal {
        span.char {
            animation: rotateWrapper 1.6s;
            animation-fill-mode: forwards;
            animation-timing-function: cubic-bezier(.785, 0.135, 0.15, 0.86);

            &:before {
                animation: revealChar 0.44s, blurIn 0.6s;
                animation-fill-mode: forwards;
                animation-timing-function: cubic-bezier(.785, 0.135, 0.15, 0.86);
            }
        }
    }
}